.gallery {
  max-width: 364px;
  max-height: 122px;

  margin-left: auto;
  margin-right: auto;
  padding-top: 97px;
  padding-bottom: 80px;
  background-image: url('../images/mobile/gallery_img1_mobile.png');

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  left: -18px;
}

