.header {
  background-color: $background_header-color;
  padding-top: 6px;
  min-width: 320px;
  border: solid 1px green;
}

.header__logo {
  @extend .link;
  width: 200px;
  border: solid 1px green;
}

.header__mobile-menu__button {
  width: 40px;
  height: 30px;
  cursor: pointer;
  border: solid 1px green;
  background-color: $background_header-color;
}

.header__logo--geometry {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: solid 1px green;
}

.header__mobile-menu__button__icon {
  width: 20px;
  height: 12px;
  fill: $brand-color;
  border: solid 1px green;
}

.hero__background {
  background-color: $text-color;
  width: 346px;
  height: 344px;
  border-radius: 50%;
  position: absolute;
  left: 36px;
  top: 83px;
}

.hero__background-image {
  background: url(../images/mobile/header_img1_mobile.png);
  position: absolute;
  width: 171px;
  height: 410px;
  background-repeat: no-repeat;
  left: 117px;
  top: 92px;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.hero--geometry {
  padding-top: 137px;
  margin-bottom: 19px;
  position: relative;
  overflow: hidden;
  border: solid 1px green;
}

.hero__text-white {
  font-family: $main-font-family;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $background-color;
  margin: 0;
  border: solid 1px green;
}
.hero__text-red {
  font-size: 47px;
  font-family: $main-font-family;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $brand-color;
  margin: 0;
  border: solid 1px green;
}

.hero__list {
  @extend .list;
  display: flex;
  padding: 0;
  padding-bottom: 140px;
  position: relative;
  border: solid 1px green;
}

.hero__item {
  padding-right: 8px;
}

.hero__link-1 {
  @extend .link;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $secondary-font-family;
  font-size: 8px;
  line-height: 1.25;
  color: $background-color;
  background: $brand-color;
  border-radius: 20px;
  width: 63px;
  height: 24px;

  &:hover,
  &:focus {
    background: $brand-color-hover;
  }
}
.hero__link-2 {
  @extend .link;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $secondary-font-family;
  font-size: 8px;
  line-height: 1.25;
  color: $brand-color;
  background: $background-color;
  border-radius: 20px;
  width: 83px;
  height: 24px;

  &:hover,
  &:focus {
    background: $background-color-hover;
  }
}

.mobile-menu {
  background-color: $mobile-menu-color;
}

.mobile-menu__btn-close {
  width: 40px;
  height: 30px;
  cursor: pointer;
  background: $mobile-menu-color;
  border: solid 1px green;
}

.mobile-menu__btn-close__icon {
  width: 14px;
  height: 14px;
  fill: $background-color;
  border: solid 1px green;
}

.mobile-menu__btn-buy {
  width: 209px;
  height: 44px;
  border-radius: 22px;
  background: #ffffff;
  border: none;

  font-family: $secondary-font-family;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.31;
  color: $brand-color;

  cursor: pointer;

  &:hover,
  &:focus {
    background: $background-color-hover;
  }
}

.nav__list {
  @extend .list;
}

.nav__item {
  @extend .link;
  font-family: $secondary-font-family;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.29;
  color: $background-color;

  &:hover,
  &:focus {
    color: $brand-color-hover;
  }
}
